<template>
    <div class="box">
        <div class="massGrp">
            <h2>Mass Download</h2>
            <input placeholder="URL" ref="URL" id="URL">
            <input placeholder=".exa, .mp, .le" ref="extentions">
            <button ref="send" @click="send">Send</button>
        </div>
    </div>
    <RadioLink 
        v-for="(link, index) in links"
        :url="link.url"
    />
</template>

<style>
.massGrp {
    #URL {
        width: 70%;
    }
    #extentions {
        width: 10%;
    }
}
</style>

<script>
import { RadioLink } from "./RadioLink.vue";
export default 
{
    data() 
    {
        return {
            links: [{url:"fdssaf"}, {url:"gklhasd"}]
        }
    },
    components: 
    {
        RadioLink
    },
    methods: 
    {
        send()  
        {     
            var extentions = this.$refs.extentions.value.split(",");
            var url = this.$refs.URL.value;
            fetch("/mass", 
            {
                method: "POST", 
                headers: 
                {
                    "Content-type" : "application/json",
                },
                body: JSON.stringify(
                    {
                        "URL" : url,
                        "extentions" : extentions
                    })
            }).then(async (res) => 
            {
                console.log(this.links);
                let linksFound = await res.json();
                this.links = linksFound;
                console.log(this.links);
            })
        }
    }           
}
</script>